import { request, requestDownload } from 'utils';

const prefix = '/api/education';

export const getClassGrading = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  semesterId,
  yearEduId,
  programDetailId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/ClassGrading/',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      semesterId,
      yearEduId,
      programDetailId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const createClassGrading = data =>
  request({
    prefix,
    url: '/ClassGrading',
    method: 'POST',
    params: {},
    data,
  });

export const lockOrUnlockClassGrading = data =>
  request({
    prefix,
    url: '/ClassGrading',
    method: 'PUT',
    params: {},
    data,
  });

export const exportExcelClassGrading = ({
  classId,
  programDetailId
}) =>
  requestDownload({
    prefix,
    defaultFileName: 'im lp hnh chnh.pdf',
    url: '/ClassGradePartGrading/ExportClassGradePartGrading',
    method: 'POST',
    data: {
      classId,
      programDetailId
    },
  });



