import React, { Component } from 'react';
import { Row, Col, Form } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { BtnSearch, BtnClear } from 'components/common/button';
import {
  getSubjectByProgramDetail,
} from 'services/programDefinition';
import { requiredRule } from '../../../utils/validateRules';
import SelectAjax from '../../../components/common/input/SelectAjax';
import { updateQueryParams, getQueryParams } from '../../../utils/urlUtil';

@Form.create({ name: 'FormSearch' })
class CreditClass extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    const params = getQueryParams();
    if (params.subjectIds) {
      params.subjectIds = params.subjectIds.split(',');
    }
    form.setFieldsValue(params);
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  render() {
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn H!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Kha',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn CN',
          },
        ],
      },
      classId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Lp',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Nm hc',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Hc k',
          },
        ],
      },
    };
    const classId = getFieldValue('classId');
    const yearEduId = getFieldValue('yearEduId');
    const semesterId = getFieldValue('semesterId');

    return (
      <Form className="form-inline-label form-search" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <YearSemesterInput form={this.props.form} optionFieldDecorator={optionFieldDecorator} span={5} />
          <Col span={5}>
            <Form.Item label="Hc phn">
              {
                getFieldDecorator('programDetailId', {
                  rules: [requiredRule],
                })
                  (
                    <SelectAjax
                      keyValue="value"
                      keyLabel="label"
                      setFieldsValue={setFieldsValue}
                      service={getSubjectByProgramDetail}
                      params={{
                        classId,
                        yearEduId,
                        semesterId,
                      }}
                      allowGetData={
                        !!yearEduId &&
                        !!semesterId &&
                        !!classId
                      }
                      typeValue="int"
                    />
                  )
              }
            </Form.Item>
          </Col>
          <Col span={5} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default CreditClass;



