import React, { Component } from 'react';
import {
  Form,
  message,
} from 'antd';
import { InputNumberScore10, TableForm, SelectTable } from 'components/common';
import { getGradeNote } from 'services/gradeNote';

@Form.create({ name: 'formTrainingPlanTableDetail' })
class TrainingPlanTableDetail extends Component {
  constructor(props) {
    super(props);
    this.refTable = React.createRef();
    this.state = {
      grandeNote: [],
    }
  }

  componentWillMount() {
    Promise.all([
      getGradeNote({}),
    ])
      .then((responses) => {
        this.setState({
          grandeNote: [...responses[0].data.items],
        });
      })
      .catch(() => {
        message.error('C li xy ra');
      });
  }

  getTableData = () => {
    return this.refTable.current.getTableData();
  }

  createColumns = () => {
    const columns1 = [
      {
        title: 'M sinh vin',
        dataIndex: 'studentCode',
        key: 'studentCode',
      },
      {
        title: 'H v tn',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Lp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Hc phn',
        dataIndex: 'subjectLabel',
        key: 'subjectLabel',
      },
      {
        title: 'TBCBP',
        dataIndex: 'subtotal',
        key: 'subtotal',
      },
      {
        title: 'Thi (60)',
        dataIndex: 'grade',
        render: (_text, record) => {
          const { getFieldDecorator } = this.props.form;
          return (
            <Form.Item>
              {getFieldDecorator(`grade-${record.autoKey}`, {
                initialValue: record.grade,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng chn im thi!',
                  },
                ],
              })(<InputNumberScore10 style={{ width: '75px' }} disabled={record.isLocked} />)}
            </Form.Item>
          );
        },
      },
      {
        title: 'Thang 4',
        dataIndex: 'totalIn4',
        key: 'totalIn4',
      },
      {
        title: 'Tng im',
        dataIndex: 'total',
        key: 'total',
      },
      {
        title: 'Ghi ch',
        dataIndex: 'gradeNoteId',
        key: 'gradeNoteId',
        render: (_text, record) => {
          const { getFieldDecorator } = this.props.form;
          return (
            <Form.Item>
              {getFieldDecorator(`gradeNoteId-${record.autoKey}`, {
                initialValue: record.gradeNoteId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng chn im ghi ch!',
                  },
                ],
              })(
                <SelectTable
                  keyValue="id"
                  keyLabel="label"
                  data={this.state.grandeNote}
                  disabled={record.isLocked}
                />
              )}
            </Form.Item>
          );
        },
      },
    ];
    return columns1;
  };

  columns = this.createColumns();

  render() {
    const { reload, rowKey, data, handleSelectedRows, form } = this.props;
    return (
      <TableForm
        reload={reload}
        data={data}
        handleSelectedRows={handleSelectedRows}
        rowKey={rowKey}
        columns={this.columns}
        form={form}
        ref={this.refTable}
      />
    );
  }
}

export default TrainingPlanTableDetail;



